const Box = ({ rowNum, boxNum, boxData, circle, topColorRed }) => {
  const evenRow = rowNum % 2 === 0;
  const evenBox = boxNum % 2 === 0;
  const boxColor =
    (evenRow && evenBox) || (!evenRow && !evenBox) ? "black" : "white";
  if (boxData !== "o") {
    const pieceColor = topColorRed ? "red" : "black";
    const pieceShape = circle ? "circle" : "";
    const piece =
      boxData !== "o" ? (
        <div className={`piece--${pieceColor} ${pieceShape}`}></div>
      ) : null;
  }

  return <div className={`box--${boxColor}`}>{piece || null}</div>;
};

export default Box;
